<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div>
            <div class="div_inline">
              :
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder=""
                v-model="seekCompany"
                @keyup.enter.native="getUserList"
                @clear="getUserList"
              />
            </div>
            <div class="div_inline">
              :
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder=""
                v-model="seekName"
                @keyup.enter.native="getUserList"
                @clear="getUserList"
              />
            </div>
            <div class="div_inline">
              <!--  -->
              <el-button
                @click="getUserList"
                class="el-button_co"
                type="primary"
                icon="el-icon-search"
              ></el-button>
            </div>
            <div class="div_inline div_right">: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <div>
            <div class="div_inline_button">
              <!--  -->
              <el-button
                @click="isAdduser"
                class="el-button_co"
                type="primary"
                icon="el-icon-circle-plus-outline"
              ></el-button>
              <el-dialog title="" :visible.sync="adduser.dialogVisible" width="30%">
                <div class="titleMessage">1.</div>
                <div class="basicMessage">
                  <div>
                    <b>:</b>
                    <em>
                      <el-select v-model="adduser.companyData" slot="prepend" placeholder="">
                        <el-option
                          v-for="item in companyList"
                          :key="item.company_id"
                          :label="item.company_name"
                          :value="item.company_id"
                        ></el-option>
                      </el-select>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="adduser.username"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-select v-model="adduser.sex" slot="prepend" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                      </el-select>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" type="password" v-model="adduser.password"></el-input>
                    </em>
                  </div>

                  <div>
                    <b>:</b>
                    <em>
                      <el-date-picker
                        type="date"
                        placeholder=""
                        v-model="adduser.AbirthData"
                        style="width: 100%;"
                      ></el-date-picker>
                    </em>
                  </div>
                </div>
                <div class="titleMessage">
                  2.
                  <span>(jpg/png500kb)</span>
                </div>
                <div class="upPicture">
                  <span class="isPicture">:</span>
                  <em>
                    <!-- <div class="uploadPicture">
                      <el-upload class="upload-demo"
                                action="https://jsonplaceholder.typicode.com/posts/"
                                :on-preview="handlePreview"
                                :on-remove="handleRemove"
                                :before-remove="beforeRemove"
                                multiple
                                :limit="3"
                                :on-exceed="handleExceed"
                                :file-list="fileList">
                        <el-button size="small"
                                  type="primary"></el-button>
                      </el-upload>
                    </div>-->
                    <!-- <img src="../../assets/img/image_bg.png" /> -->

                    <el-upload
                      :action="UploadUrl"
                      list-type="picture-card"
                      :on-preview="handlePictureCardPreview"
                      :on-remove="handleRemove"
                    >
                      <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt />
                    </el-dialog>
                  </em>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="adduser.dialogVisible = false"> </el-button>
                  <el-button type="primary" @click="isconfirm"> </el-button>
                </span>
              </el-dialog>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button
                @click="isDel"
                class="el-button_co"
                type="danger"
                icon="el-icon-remove-outline"
              ></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button
                class="el-button_co el-button_coco"
                type="primary"
                icon="el-icon-s-custom"
              ></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button class="el-button_co" type="primary" icon="el-icon-upload2"></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button class="el-button_co" type="primary" icon="el-icon-download"></el-button>
            </div>
          </div>
        </el-col>
      </el-row>

      <el-row>
        <!--  -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
          stripe
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="" align="center" type="index" width="55"></el-table-column>
          <el-table-column prop="company_name" label="" align="center"></el-table-column>
          <el-table-column prop="username" label="" align="center"></el-table-column>
          <el-table-column prop="sex" label="" align="center"></el-table-column>
          <el-table-column prop="birthday" label="" align="center"></el-table-column>
          <el-table-column prop label="" align="center">
            <template slot-scope="scope">
              <!--  (data)-->
              <i style="padding: 0 0.1rem;" @click="isEdit(scope)">
                <img src="../../assets/img/a_edit01.png" />
              </i>
              <el-dialog title="" :visible.sync="edituser.dialogVisible" width="28%">
                <div class="titleMessage">1.</div>
                <div class="basicMessage">
                  <div>
                    <b>id:</b>
                    <em>
                      <el-input
                        size="small"
                        clearable
                        placeholder="id"
                        v-model="adduser.id"
                        :disabled="true"
                      ></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input
                        size="small"
                        clearable
                        placeholder=""
                        v-model="adduser.username"
                      ></el-input>
                    </em>
                  </div>
                  <!-- <div><b>:</b>
                    <em>
                      <el-input placeholder=""
                                type="password"
                                v-model="adduser.password"></el-input>
                    </em>
                  </div>-->
                  <div>
                    <b>:</b>
                    <em>
                      <el-select
                        v-model="adduser.sex"
                        slot="prepend"
                        placeholder=""
                        style="left:-0.75rem"
                      >
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                      </el-select>
                    </em>
                  </div>
                  <!-- <div><b>:</b>
                    <em>
                      <el-date-picker type="date"
                                      placeholder=""
                                      v-model="adduser.AbirthData"
                                      style="width: 100%;"></el-date-picker>
                    </em>
                  </div>-->
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="adduser.dialogVisible = false"> </el-button>
                  <el-button type="primary" @click="isEdituser"> </el-button>
                </span>
              </el-dialog>
              <i style="padding: 0 0.1rem;" @click="isImage(scope)">
                <img src="../../assets/img/a_image01.png" />
              </i>
              <i style="padding: 0 0.1rem;" @click="isInfo(scope)">
                <img src="../../assets/img/a_info01.png" />
              </i>
              <i style="padding: 0 0.1rem;" @click="isDel(scope)">
                <img src="../../assets/img/a_del01.png" />
              </i>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!--  -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
// import Cascader from 'element-ui'
// import url from '@/http/http'
export default {
  data() {
    return {
      // 
      listdata: [],
      // 
      companyList: [],
      // 
      seekCompany: '',
      seekName: '',
      // 
      adduser: {
        // 
        dialogVisible: false,
        // 
        isbirthData: false,
        AbirthData: '',
        username: '',
        companyData: '',
        sex: '',
        password: '',
        id: '',
        isSex: ''
      },
      // 
      edituser: {
        dialogVisible: false
      },
      isToken: '',
      fileList: [],
      dialogImageUrl: '',
      dialogVisible: false,
      UploadUrl: 'http://localhost:8080/',
      delId: [],
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getUserList()
    //  web http://127.0.0.1:8080/testdemo/test.html?id=1&name=test
    var url = window.location.href
    // /testdemo/test.html
    var pathname = window.location.pathname
    //  web 8080
    var port = window.location.port
    //  web http:
    var protocol = window.location.protocol
    // ?id=1&name=test
    var search = window.location.search
    // 
    console.log(url)
  },
  methods: {
    // 
    getUserList() {
      var apiUrl =
        '/api' +
        '/user/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekCompany) {
        apiUrl += '&company_name=' + this.seekCompany
      }
      if (this.seekName) {
        apiUrl += '&username=' + this.seekName
      }

      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
    },
    // 
    isAdduser() {
      this.adduser.dialogVisible = true
      // 
      this.$http
        .get('/api' + '/company/find/list')
        .then(res => {
          this.companyList = res.data.data.list
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
    },
    // 
    birthData() {
      this.adduser.isbirthData = true
    },
    // 
    handleRemove(file, fileList) {
      console.log(file, fileList)
    },
    handlePreview(file) {
      console.log(file)
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 3  ${
          files.length
        }  ${files.length + fileList.length} `
      )
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`)
    },
    // 
    isconfirm() {
      console.log(typeof this.adduser.AbirthData)
      console.log(this.adduser.AbirthData)
      return false
      // eslint-disable-next-line no-unreachable
      console.log('')
      this.$http
        .post(
          '/api' + '/user/save',
          {
            company_id: this.adduser.companyData,
            password: this.adduser.password,
            sex: this.adduser.sex,
            birthday: this.adduser.AbirthData,
            username: this.adduser.username
          }
        )
        .then(res => {
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {})
      // eslint-disable-next-line no-unreachable
      this.adduser.dialogVisible = false
    },
    // 
    isEdit(scope) {
      this.edituser.dialogVisible = true
      console.log(scope)
      this.adduser.id = scope.row.user_id
      this.adduser.username = scope.row.username
      this.adduser.sex = scope.row.sex
      this.adduser.AbirthData = scope.row.birthday
    },
    // 
    isEdituser() {
      this.$http
        .post(
          '/api' + '/user/update',
          {
            user_id: this.adduser.id,
            username: this.adduser.username,
            sex: this.adduser.sex
          }
        )
        .then(res => {
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
      this.edituser.dialogVisible = false
      this.getUserList()
    },
    // 
    isImage() {},
    // 
    isInfo() {},
    // 
    handleSelectionChange(val) {
      val.forEach((ele, indx) => {
        this.delId.push(ele.user_id)
      })
    },
    // 
    async isDel(scope) {
      if (scope.row) {
      } else if (!this.delId.length) {
        this.$message({ message: '!', type: 'warning' })
        return false
      }
      const confirmResult = await this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      if (scope.row) {
        this.delId.push(scope.row.user_id)
      }
      this.$http
        .post('/api' + '/user/del', this.delId)
        .then(res => {
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
      this.getUserList()
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
      console.log(file.url)
    },
    // 
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.getUserList(this.currentPage)
    }
  }
}
</script>
<style lang="less">
.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
</style>

