<template>
  <div>
    <el-card class="g_label">
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="div_inline">
            IP/:
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:160px"
              placeholder="IP/"
              v-model="seekIP"
            />
          </div>
          <div class="div_inline">
            :
            <el-input
              size="small"
              clearable
              class="el-input_co"
              style="width:155px"
              placeholder=""
              v-model="seekSerialNo"
            />
          </div>
          <div class="div_inline">
            <!--  -->
            <el-button
              @click="getList"
              class="el-button_co"
              type="primary"
              icon="el-icon-search"
            ></el-button>
          </div>
          <div class="div_inline div_right">
            <div class="div_inline">: {{pageTotal}}</div>
            <div class="div_inline">: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <div class="div_inline_button">
            <!--  -->
            <el-button
              @click="isAddDevice"
              class="el-button_co"
              type="primary"
              icon="el-icon-circle-plus-outline"
            ></el-button>
            <el-dialog title="" :visible.sync="addDevice.dialogVisible" width="30%">
              <div class="basicMessage">
                <div>
                  <b>:</b>
                  <em>
                    <el-input placeholder="" v-model="addDevice.CompanyName"></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-select v-model="addDevice.DeviceType" slot="prepend" placeholder="">
                      <el-option label="" value="facePlate"></el-option>
                      <el-option label="" value="camera"></el-option>
                    </el-select>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-input placeholder="" v-model="addDevice.DeviceAddress"></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-input placeholder="" v-model="addDevice.DevicePort"></el-input>
                  </em>
                </div>
                <div>
                  <b>IP/:</b>
                  <em>
                    <el-input placeholder="IP/" v-model="addDevice.DeviceIp"></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-input placeholder="" v-model="addDevice.DeviceSeria"></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-input placeholder="" v-model="addDevice.DeviceName"></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-input
                      placeholder=""
                      v-model="addDevice.DevicePassword"
                      type="password"
                    ></el-input>
                  </em>
                </div>
                <div>
                  <b>:</b>
                  <em>
                    <el-select
                      v-model="addDevice.ProtocolType"
                      slot="prepend"
                      placeholder=""
                    >
                      <el-option label="" value="private"></el-option>
                      <el-option label="rtsp" value="rtsp"></el-option>
                    </el-select>
                  </em>
                </div>
              </div>
              <span slot="footer" class="dialog-footer">
                <el-button @click="addDevice.dialogVisible = false"> </el-button>
                <el-button type="primary" @click="isconfirm"></el-button>
              </span>
            </el-dialog>
          </div>
          <div class="div_inline_button">
            <!--  -->
            <el-button
              @click="isDel"
              class="el-button_co"
              type="danger"
              icon="el-icon-remove-outline"
            ></el-button>
          </div>
            <div class="div_inline_button">
            <!--  -->
              <el-button
                class="el-button_co"
                type="primary"
                icon="el-icon-upload2"
              ></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button
                class="el-button_co"
                type="primary"
                icon="el-icon-download"
              ></el-button>
            </div>
        </el-col>
      </el-row>
      <el-row>
        <!--  -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="" align="center" type="index" width="55"></el-table-column>
          <el-table-column prop="device_ip" label="IP" align="center"></el-table-column>
          <el-table-column prop="device_name" label="" align="center"></el-table-column>
          <el-table-column prop="serial" label="" align="center"></el-table-column>
          <el-table-column prop="device_enabled" label="" align="center">
            <template slot-scope="scope">
              <i v-if="scope.row.device_enabled==0">
                <img src="../../assets/img/on_off02.png" />
              </i>
              <i v-else-if="scope.row.device_enabled==1">
                <img src="../../assets/img/on_off01.png" />
              </i>
            </template>
          </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <!--  -->
              <i style="padding: 0 0.08rem;" @click="isEdit(scope)">
                <img src="../../assets/img/a_edit01.png" />
              </i>
              <el-dialog title="" :visible.sync="isEditdialogVisible" width="30%">
                <div class="basicMessage">
                  <div>
                    <b>ID:</b>
                    <em>
                      <el-input v-model="scope.row.device_id" disabled></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.DeviceAddress"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>IP:</b>
                    <em>
                      <el-input placeholder="IP" v-model="addDevice.DeviceIp"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.DevicePort"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.DeviceName"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.DevicePassword"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.DeviceSeria"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addDevice.CompanyName"></el-input>
                    </em>
                  </div>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="isEditdialogVisible= false"> </el-button>
                  <el-button type="primary" @click="isEditconfirm"></el-button>
                </span>
              </el-dialog>
              <!--  -->
              <i style="padding: 0 0.08rem;" @click="isDel(scope)">
                <img src="../../assets/img/a_del201.png" />
              </i>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!--  -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      listdata: [],
      // 
      addDevice: {
        dialogVisible: false,
        CompanyName: '',
        DevicePort: '',
        DeviceName: '',
        DevicePassword: '',
        DeviceAddress: '',
        DeviceIp: '',
        DeviceSeria: '',
        DeviceID: '',
        DeviceType: '',
        ProtocolType: ''
      },
      // ip
      seekIP: '',
      seekSerialNo: '',
      isToken: '',
      isEditdialogVisible: false,
      delId: [],
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getList()
  },
  methods: {
    getList() {
      var apiUrl =
        '/api' +
        '/device/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekIP) {
        apiUrl += '&deviceIp=' + this.seekIP
      }
      if (this.seekSerialNo) {
        apiUrl += '&serialNo=' + this.seekSerialNo
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
    },
    // 
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    // 
    isAddDevice() {
      this.addDevice.dialogVisible = true
    },
    // 
    isconfirm() {
      this.$http
        .post(
          '/api' + '/device/add ',
          {
            device_ip: this.addDevice.DeviceIp,
            device_port: this.addDevice.DevicePort + 0,
            device_name: this.addDevice.DeviceName,
            device_password: this.addDevice.DevicePassword,
            device_address: this.addDevice.DeviceAddress,
            serial: this.addDevice.DeviceSeria,
            company_name: this.addDevice.CompanyName,
            protocolType: this.addDevice.ProtocolType,
            deviceType: this.addDevice.DeviceType
          }
        )
        .then(res => {
          this.getList()
          // 
          this.addDevice.dialogVisible = false
          this.addDevice.DeviceIp = null
          this.addDevice.DevicePort = null
          this.addDevice.DeviceName = null
          this.addDevice.DevicePassword = null
          this.addDevice.DeviceAddress = null
          this.addDevice.DeviceSeria = null
          this.addDevice.CompanyName = null
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
    },
    // 
    isDelCompany() {},
    // 
    handleSelectionChange(val) {
      val.forEach((ele, indx) => {
        this.delId.push(ele.device_id)
      })
    },
    // 
    isEdit(scope) {
      this.isEditdialogVisible = true
      console.log(scope.row)
      this.addDevice.DeviceIp = scope.row.device_ip
      this.addDevice.DevicePort = scope.row.device_port
      this.addDevice.DeviceName = scope.row.device_name
      this.addDevice.DevicePassword = scope.row.device_password
      this.addDevice.DeviceSeria = scope.row.serial
      this.addDevice.CompanyName = scope.row.company_name
      this.addDevice.DeviceID = scope.row.device_id
      this.addDevice.DeviceAddress = scope.row.device_address
    },
    // 
    isEditconfirm() {
      this.$http
        .post(
          '/api' + '/device/update',
          {
            device_id: this.addDevice.DeviceID,
            device_ip: this.addDevice.DeviceIp,
            device_port: this.addDevice.DevicePort + 0,
            device_name: this.addDevice.DeviceName,
            device_password: this.addDevice.DevicePassword,
            device_address: this.addDevice.DeviceAddress,
            serial: this.addDevice.DeviceSeria,
            company_name: this.addDevice.CompanyName,
            protocolType: this.addDevice.ProtocolType,
            deviceType: this.addDevice.DeviceType
          }
        )
        .then(res => {
          this.isEditdialogVisible = false
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
      this.getList()
    },
    // 
    async isDel(scope) {
      if (scope.row) {
        this.delId.push(scope.row.device_id)
        console.log(this.delId)
      } else if (!this.delId.length) {
        this.$message({ message: '!', type: 'warning' })
        return false
      }
      const confirmResult = await this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http
        .post('/api' + '/device/del', this.delId)
        .then(res => {
          this.getList()
          console.log(res)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('')
        })
    }
  }
}
</script>
<style lang="less" scoped>
.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
</style>
