<template>
  <div>
    <el-card>
      <el-row justify="space-between" type="flex" :gutter="20" class="oneRow">
        <el-col :span="24">
          <div class="g_label">
            <div class="div_inline">
              :
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder=""
                v-model="seekCompanyName"
                @keyup.enter.native="getList"
                @clear="getList"
              />
            </div>
            <div class="div_inline">
              :
              <el-input
                size="small"
                clearable
                class="el-input_co"
                style="width:150px"
                placeholder=""
                v-model="seekCompanyAddress"
                @keyup.enter.native="getList"
                @clear="getList"
              />
            </div>
            <div class="g_label div_inline">
              <!--  -->
              <el-button
                @click="getList"
                class="el-button_co"
                type="primary"
                icon="el-icon-search"
              ></el-button>
            </div>
            <div class="g_label div_inline div_right">: {{pageTotal}}</div>
          </div>
        </el-col>
      </el-row>
      <el-row justify="space-between" type="flex" :gutter="20" class="twoRow">
        <el-col :span="24">
          <div class="g_label">
            <div class="div_inline_button">
              <!--  -->
              <el-button
                @click="isAddCompany"
                class="el-button_co"
                type="primary"
                icon="el-icon-circle-plus-outline"
              ></el-button>
              <el-dialog title="" :visible.sync="addCompany.dialogVisible" width="30%">
                <div class="basicMessage">
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyName"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyAddress"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyTel"></el-input>
                    </em>
                  </div>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="addCompany.dialogVisible = false"> </el-button>
                  <el-button type="primary" @click="isconfirm"></el-button>
                </span>
              </el-dialog>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button
                @click="isDel"
                class="el-button_co"
                type="danger"
                icon="el-icon-remove-outline"
              ></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button class="el-button_co" type="primary" icon="el-icon-upload2"></el-button>
            </div>
            <div class="div_inline_button">
              <!--  -->
              <el-button class="el-button_co" type="primary" icon="el-icon-download"></el-button>
            </div>
          </div>
        </el-col>
      </el-row>

      <el-row>
        <!--  -->
        <el-table
          style="width: 100%"
          :header-cell-style="{background:'#eee',color:'#000'}"
          :data="listdata"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="" align="center" type="index" width="55"></el-table-column>
          <el-table-column prop="company_name" label="" align="center"></el-table-column>
          <el-table-column prop="company_address" label="" align="center"></el-table-column>
          <el-table-column prop="company_tel" label="" align="center"></el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <!--  -->
              <i style="padding: 0 0.08rem;" @click="isEdit(scope)">
                <img src="../../assets/img/a_edit01.png" />
              </i>
              <el-dialog title="" :visible.sync="isEditdialogVisible" width="30%">
                <div class="basicMessage">
                  <div>
                    <b>ID:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyID" disabled></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyName"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyAddress"></el-input>
                    </em>
                  </div>
                  <div>
                    <b>:</b>
                    <em>
                      <el-input placeholder="" v-model="addCompany.CompanyTel"></el-input>
                    </em>
                  </div>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="isEditdialogVisible= false"> </el-button>
                  <el-button type="primary" @click="isEditconfirm"></el-button>
                </span>
              </el-dialog>
              <!--  -->
              <i style="padding: 0 0.08rem;" @click="isDel(scope)">
                <img src="../../assets/img/a_del201.png" />
              </i>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!--  -->
      <el-row>
        <div class="page_div">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="pageTotal"
          ></el-pagination>
        </div>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      listdata: [],
      // 
      addCompany: {
        dialogVisible: false,
        CompanyName: '',
        CompanyAddress: '',
        CompanyTel: '',
        CompanyID: ''
      },
      // 
      seekCompanyName: '',
      seekCompanyAddress: '',
      isToken: '',
      isEditdialogVisible: false,
      delId: [],
      currentPage: 1,
      pageSize: 13,
      pageTotal: 0
    }
  },
  created() {
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getList()
  },
  methods: {
    // 
    getList() {
      var apiUrl =
        '/api' +
        '/company/find/list' +
        '?page=' +
        this.currentPage +
        '&pageSize=' +
        this.pageSize
      if (this.seekCompanyName) {
        apiUrl += '&company_name=' + this.seekCompanyName
      }
      if (this.seekCompanyAddress) {
        apiUrl += '&company_address=' + this.seekCompanyAddress
      }
      this.$http
        .get(apiUrl)
        .then(res => {
          this.listdata = res.data.data.list
          this.pageTotal = res.data.data.total
          console.log(res)
        })
        .catch(err => {
          console.log('')
        })
    },
    // 
    handleSizeChange(val) {
      console.log(` ${val} `)
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.currentPage = val
      this.getList(this.currentPage)
    },
    date_picker_change(val) {
      console.log(`: ${val}`)
      console.log(this.seekTime)
      this.getList(this.currentPage)
    },
    //
    isAddCompany() {
      this.addCompany.dialogVisible = true
    },
    // 
    isconfirm() {
      this.$http
        .post(
          '/api' + '/company/add',
          {
            company_name: this.addCompany.CompanyName,
            company_address: this.addCompany.CompanyAddress,
            company_tel: this.addCompany.CompanyTel
          }
        )
        .then(res => {
          ;(this.addCompany.dialogVisible = false),
            // 
            (this.addCompany.CompanyName = null),
            (this.addCompany.CompanyAddress = null),
            (this.addCompany.CompanyTel = null)
        })
        .catch(err => {
          console.log('')
        })
      this.getList()
    },
    // 
    isDelCompany() {},
    // 
    handleSelectionChange(val) {
      console.log(val)
      val.forEach((ele, indx) => {
        this.delId.push(ele.company_id)
      })
      console.log(this.delId)
    },
    // 
    isEdit(scope) {
      this.isEditdialogVisible = true
      console.log(scope.row)
      this.addCompany.CompanyID = scope.row.company_id
      this.addCompany.CompanyName = scope.row.company_name
      this.addCompany.CompanyAddress = scope.row.company_address
      this.addCompany.CompanyTel = scope.row.company_tel
    },
    // 
    isEditconfirm() {
      this.$http
        .post(
          '/api' + '/company/update',
          {
            company_id: this.addCompany.CompanyID,
            company_name: this.addCompany.CompanyName,
            company_address: this.addCompany.CompanyAddress,
            company_tel: this.addCompany.CompanyTel
          }
        )
        .then(res => {
          this.isEditdialogVisible = false
        })
        .catch(err => {
          console.log('')
        })
      this.getList()
    },
    // 
    async isDel(scope) {
      if (scope.row) {
        this.delId.push(scope.row.company_id)
      } else if (!this.delId.length) {
        this.$message({ message: '!', type: 'warning' })
        return false
      }
      const confirmResult = await this.$confirm(
        '?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      this.$http
        .post('/api' + '/company/del', this.delId)
        .then(res => {
          this.getList()
          console.log(res)
        })
        .catch(err => {
          console.log('')
        })
    }
  }
}
</script>
<style lang="less" scoped>
.titleMessage {
  color: black;
  padding-bottom: 0.2rem;
  font-weight: 700;
  text-align: left;
  span {
    font-weight: 500;
    font-size: 0.1rem;
  }
}
.basicMessage {
  color: #3c4e86;
  b {
    display: inline-block;
    width: 1rem;
    text-align: right;
    height: 0.5rem;
    font-size: 0.2rem;
    padding-right: 0.1rem;
  }
  // input {
  //   height: 0.3rem;
  //   width: 3.5rem;
  // }
  em {
    width: 4rem;
    display: inline-block;
  }
  // .el-input {
  //   display: inline;
  // }
}
// 
.upPicture {
  position: relative;
  .uploadPicture {
    position: absolute;
    left: 1.17rem;
    top: 0.21rem;
    button {
      position: relative;
      width: 1rem;
      height: 0.45rem;

      span {
        position: absolute;
        left: 0.16rem;
        top: 0.13rem;
        font-size: 0.16rem;
      }
    }
  }
}
</style>


